export function code_codeGroups() {
  return `/code/GetCodeGroup_List`;
}
export function create_code_codeGroup() {
  return `/code_detail/Save_CodeGroup`;
}
export function update_code_codeGroup() {
  return `/code_detail/Save_CodeGroup`;
}
export function delete_code_codeGroup(body) {
  return {
    data: { idxs: [`${body.code_master_idx}`] },
    url: `/code/Delete_CodeGroup`,
  };
}
export function code_codes() {
  return `/code/GetCode_List`;
}
export function create_code_code() {
  return `/code_detail/Save_Code`;
}
export function update_code_code(body) {
  return `/code_detail/Save_Code`;
}
export function delete_code_code(body) {
  return {
    data: { idxs: [`${body.code_master_idx}`] },
    url: `/code/Delete_Code`,
  };
}
